<template>
  <div style="width:100%">
    <!-- 顶部toolbar -->
    <el-form ref="form1" :inline="true" class="toolbar" label-position="right" label-width="100px" :model="queryStr">
      <el-row :gutter="24">
        <el-form-item label="公司名称" class="ml15" prop="company_name">
          <el-input v-model.trim="queryStr.relation_company_name" placeholder=""></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click = "query(1)" >查询</el-button>
          <el-button type="primary" @click="handle_admin" >新建</el-button>
        </el-form-item>
      </el-row>
    </el-form>
    <!-- 列表 -->
    <el-table :data="tableData" border v-loading="loading">
     <el-table-column type="index" width="50" label="序号">
      </el-table-column>
			<el-table-column header-align="center" align="center" label="操作"  min-width="150">
				<template slot-scope="scope">
					<el-button type="primary" size="mini" @click = "edit_query(scope.row, scope.$index)">修改</el-button>
          <el-button type="danger" size="mini" @click = "del_query(scope.row, scope.$index)">删除</el-button>
				</template>
			</el-table-column>
			<el-table-column header-align="center" align="center" label="用户名称"  >
				<template slot-scope="scope">
					<span>{{scope.row.user_name}}</span>
				</template>
			</el-table-column>
      <el-table-column header-align="center" align="center" label="密码"  type="password">
				<template slot-scope="scope">
					<span>******</span>
				</template>
			</el-table-column>
			<el-table-column header-align="center" align="center" label="联系电话"  >
				<template slot-scope="scope">
					<span>{{scope.row.telphone}}</span>
				</template>
			</el-table-column>
      <el-table-column header-align="center" align="center" label="绑定公司"  >
				<template slot-scope="scope">
					<span>{{scope.row.relation_company_name}}</span>
				</template>
			</el-table-column>
      <el-table-column header-align="center" align="center" label="创建时间"  >
				<template slot-scope="scope">
					<span>{{scope.row.create_time |time}}</span>
				</template>
			</el-table-column>
       <el-table-column header-align="center" align="center" label="状态"  >
				<template slot-scope="scope">
					<span>{{scope.row.status==1?'启用':'停用'}}</span>
				</template>
			</el-table-column>
    
		</el-table>
    <!-- 底部toolbar -->
			<el-col :span="24" class="toolbar">
				<el-pagination layout="prev, pager, next, jumper" @current-change="currentChange" :page-size="10" :total="queryStr.total" style="float:right;">
				</el-pagination>
			</el-col>
    <!-- 新建管理员弹出框 -->
      <el-dialog :title="title1" :visible.sync="dialogFormOneVisible">
        <el-form label-width="120px" label-position="left" ref="form1" :model="form_one_data" :rules="rules1" >
          <el-form-item label="用户名称" class="ml15" prop="user_name">
            <el-col :span="12">
              <el-input v-model="form_one_data.user_name"></el-input>
            </el-col>
          </el-form-item>
           <el-form-item label="密码" class="ml15" prop="passwd">
            <el-col :span="12">
              <el-input v-model="form_one_data.passwd" type="password"></el-input>
            </el-col>
          </el-form-item>
           <el-form-item label="重复密码" class="ml15" prop="repasswd">
            <el-col :span="12">
              <el-input v-model="form_one_data.repasswd" type="password"></el-input>
            </el-col>
          </el-form-item>
          <el-form-item label="联系电话" class="ml15" prop="telphone">
            <el-col :span="12">
              <el-input v-model="form_one_data.telphone"></el-input>
            </el-col>
          </el-form-item>
          <el-form-item label="绑定公司" class="ml15" prop="relation_company_id" >
            <el-col :span="12">
               <el-select v-model="form_one_data.relation_company_id" placeholder="请选择">
                <el-option
                  v-for="item in options"
                  :key="item.value"
                  :label="item.company_name"
                  :value="item.id">
                </el-option>
              </el-select>
            </el-col>
          </el-form-item>
          <el-form-item label="状态" class="ml15" prop="bus_type">
            <el-col :span="12">
              <el-radio v-model="form_one_data.status" label="1">启用</el-radio>
              <el-radio v-model="form_one_data.status" label="2">停用</el-radio>
            </el-col>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormOneVisible = false">取 消</el-button>
          <el-button type="primary"  @click="add_admin">确 定</el-button>
        </div>
      </el-dialog>
  </div>
</template>
<script>
  import {companyLists,addCompanyAdmin,addCompany,adminLists,delAdmin,companyAdminDetail} from '@/api/api'
  import { tel_num } from '@/assets/utils/validate';
  export default {
    data(){
      return{
        title1:'新建管理员',
        queryStr: {
          relation_company_name:'',
          total: 1,       // 总数
          page: 1,        // 当前分页
          size: 10,      // 每页条数
        },
        form_one_data:{
          relation_company_name:'',
          relation_company_id:'',
          user_name:'',
          passwd:'',
          repasswd:'',
          telphone:'',
          status:"1",
        },
        rules1: {
            user_name: [
                { required: true, message: '请输入管理员名', trigger: 'blur' },
            ],
            passwd: [
                { required: true, message: '请输入密码', trigger: 'blur' },
            ],
            repasswd: [
                { required: true, message: '请输入确认密码', trigger: 'blur' },
            ],
            telphone: [
                { required: true, message: '请输入联系电话', trigger: 'blur' },
                { validator: tel_num, trigger: 'blur' },
            ],
            relation_company_id: [
                { required: true, message: '请绑定公司', trigger: 'blur' },
            ],
        },
        options:[],//选择国家数据
        dialogFormOneVisible: false,
        dialogTableVisible: false,  
        tableData:[],
        tableTwoData:[
          {
            id:1,
            actionTime:"张三",
            username:"18600877033",
            actionType:0,
            remark:1,
          },
        ],
        loading: false,//loading
        centerDialogVisible: false,
  
      }
    },
    mounted(){
      this.query()
    
    },
    methods:{
      // 查询
        query(run_type) { 
            let params = {
                page: this.queryStr.page,
                limit: this.queryStr.limit,
                relation_company_name:this.queryStr.relation_company_name,
            };
            adminLists(params).then((res) => {
                this.queryStr.total = Number(res.data.data.total);
                this.tableData = res.data.data.data;
            });
            
        },
        add_query(data,index){
            this.form_one_data.relation_company_id = data.id;
            this.form_one_data.relation_company_name = data.company_name;
            this.dialogFormOneVisible=true;
        },
        add_admin(){
            this.$refs['form1'].validate((valid) => {
                if (valid) {
                    this.dialogFormOneVisible =false;
                    let params  = this.form_one_data;
                    addCompanyAdmin(params).then((res) => {
                        this.dialogFormOneVisible = false;
                        this.$message.success('保存成功');
                        this.title1 = '新建管理员';
                        this.query();
                    });
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        
        },
        handle_admin(){
            this.title1 = '新建管理员';
            this.form_one_data={}
            this.form_one_data.status= '1';
            companyLists({}).then((res) => {
                this.options = res.data.data.data;
            });
            this.dialogFormOneVisible = true;
        },
        del_query(val,index){ //删除
            let id = val.id;
            let params = {
                'id':id,
            }
            delAdmin(params).then((res) => {
                this.$message.success('【删除】操作成功!');
               this.query();
            });
        },
        // 分页器
        currentChange(val) {

            this.queryStr.page = val;
            this.query();
        },
        // 修改 yjq
        edit_query(row, index){
            this.title1 = '修改管理员';
            let id = row.id;
            let params = {
                'id':id,
            }
            companyLists({}).then((res) => {
                this.options = res.data.data.data;
            });
            companyAdminDetail(params).then((res) => {
                this.form_one_data = res.data.data;
                this.dialogFormOneVisible = true;
            });

        },
        // 时间戳的转换
        changeTime(time){
            let year = new Date((time)).getFullYear();
            let month = new Date((time)).getMonth()+1 < 10?'0'+(new Date((time)).getMonth()+1):(new Date((time)).getMonth()+1);
            let date = new Date((time)).getDate() < 10 ?'0'+new Date((time)).getDate():new Date((time)).getDate();
            let hours = new Date((time)).getHours() < 10 ?'0'+new Date((time)).getHours():new Date((time)).getHours();
            let minutes = new Date((time)).getMinutes() < 10 ?'0'+new Date((time)).getMinutes():new Date((time)).getMinutes();
            let seconds = new Date((time)).getSeconds() < 10 ?'0'+new Date((time)).getSeconds():new Date((time)).getSeconds();
            return year+'-'+month+'-'+date+' '+hours+':'+minutes+':'+seconds
        }
    }
  }
</script>
<style scoped lang="scss">
  .el-radio-group {
		display: -webkit-flex;
		flex-wrap: wrap;
		label {
			flex-basis: 25%;
			margin-left:0 ;
			line-height: 30px;
		}
	}

  
</style>

<style>
.select  .el-input__inner{
    width:203px;
  }
</style>





